import axios from "axios";
import { initialState } from "../initialState";
import { types } from "../types"
import { HideLoading, SetLoading } from "./loading";

const per_page = initialState.per_page

export const getAllPosts = () => {
    return async (dispatch) => {

        dispatch(SetLoading())
        const { data } = await axios(`/wp-json/wp/v2/posts`)
        dispatch(HideLoading())

        dispatch({
            type: types.SetProducts,
            payload: {
                allProducts: data.length
            }
        })
    }
}

export const getPosts = ( page = 1) => {
    return async (dispatch) => {
        dispatch(SetLoading())
        const { data } = await axios(`/wp-json/wp/v2/posts?per_page=${per_page}&page=${page}`)
        dispatch(HideLoading())

        dispatch({
            type: types.SetProducts,
            payload: {
                products: data,
                search: false
            }
        })
    }
}

export const getPostsInCategory = (category , page = 1) => {
    return async (dispatch) => {

        dispatch(SetLoading())
        const { data } = await axios(`/wp-json/wp/v2/posts?categories=${category}&per_page=${per_page}&page=${page}`)
        dispatch(HideLoading())

        dispatch({
            type: types.SetCategoryProducts,
            payload: {
                id: category,
                products: data
            }
        })
    }
}

export const SearchPosts = (search, page = 1) => {
    return async (dispatch) => {

        dispatch(SetLoading())
        const allSearchProducts = await axios(`http://react/wp-json/wp/v2/search/?search=${search}&type=post`)
        const { data } = await axios(`http://react/wp-json/wp/v2/search/?_embed&search=${search}&type=post&per_page=${per_page}&page=${page}`)
        let posts = data.map(post => post._embedded['self'][0])
        dispatch(HideLoading())

        dispatch({
            type: types.SetProducts,
            payload: {
                products: posts,
                search: true,
                searchQuery: search,
                allSearchProducts: allSearchProducts.data.length
            }
        })
    }
}