import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { types } from '../../redux/types';

export const SelectCategory = () => {

    const dispatch = useDispatch()
    const categories = useSelector(state => state.categories)
    const allProducts = useSelector(state => state.allProducts)


    return (
        <div className="btn-group categoriesList-mobile" role="group">
            <button id="btnGroupDrop1" type="button" className="btn c-grey dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                Категории
                </button>
            <ul className="dropdown-menu" aria-labelledby="btnGroupDrop1">
                <li className="dropdown-item d-flex justify-content-between align-items-center">
                    <NavLink onClick={() => dispatch({ type: types.SetSearch })} to={`/`} >
                        Все иснтрументы
                    </NavLink>
                    <span className="badge bg-primary rounded-pill">{allProducts}</span>
                </li>
                {categories.map((cat, index) => {
                    if (cat.count)
                        return (
                            <li key={index} className="dropdown-item d-flex justify-content-between align-items-center">
                                <NavLink to={`/category/${cat.id}`} >
                                    {cat.name}
                                </NavLink>
                                <span className="badge bg-primary rounded-pill">{cat.count}</span>
                            </li>
                        )
                })}
            </ul>
        </div>
    )
}