import { types } from "../types"

export const SetLoading = () => {
    return {type: types.SetLoading}
}

// export const HideLoading = () => {
//     return {type: types.HideLoading}
// }

export const HideLoading = () => {
    return async (dispatch) => {
        setTimeout(() => {
            dispatch({type: types.HideLoading})
        }, 500)
    }
}