import axios from "axios"
import { types } from "../types"
import { HideLoading, SetLoading } from "./loading"

export const sendContactForm = (order) => {
    return async (dispatch) => {
        const data = { ...order.product, ...order.user }

        const form = new FormData();
        for (const field in data)
            form.append(field, data[field])

        const req = {
            method: 'post',
            url: "/wp-json/contact-form-7/v1/contact-forms/12/feedback",
            headers: {},
            data: form
        }

        dispatch(SetLoading())
        const res = await axios(req)
        dispatch(HideLoading())

        const color = res.data.status === 'mail_sent' ? 'success' : 'danger'
        dispatch({
            type: types.AddToast,
            payload: {text: res.data.message, color}
        })

    }
}



