import React from 'react';
import renderHTML from 'react-render-html';

export const Addition = ({ addition }) => {
    const imgUrl = addition._embedded['wp:featuredmedia'] && addition._embedded['wp:featuredmedia']['0'].source_url

    return (
        <div className="addition "> {/* col-lg-6 col-md-12 */}
            <div className="card mb-3" >
                <div className="row g-0">
                    <div className="col-md-5">
                        <div className="card-img">
                            <img src={imgUrl} className="card-img-top" alt="..." />
                        </div>
                    </div>
                    <div className="col-md-7 card-description">
                        <div className="card-body">
                            <h4 className="card-title">{renderHTML(addition.title.rendered)}</h4>
                            <p className="card-text">{renderHTML(addition.content.rendered)}</p>
                            {addition.acf.descriptionList &&
                                <ul className="description-list">
                                    {addition.acf.descriptionList.map((item, index) => <li className="description-list-item" key={index}>{item}</li>)}
                                </ul>}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}