import { Button } from 'bootstrap';
import React from 'react';
import { MyButton } from './UI/Button';

export const Banner = () => {
    const btnCatalog = {
        btn: false,
        textBtn: "Смотреть каталог",
        classBtn: "primary",
        sizeBtn: "large",
        link: "#products",
        additClasses: "mr-3"
    }

    const btnConsul = {
        btn: false,
        textBtn: "Получить консультацию",
        classBtn: "grey",
        sizeBtn: "large",
        link: "tel:89051027358",
    }

    return (
        <div id="banner">
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="banner-textblock">
                            <h1><span>Аренда</span> <br /> инструмента и оборудования в Брянске<span style={{display: "none"}}>электро-бензооборудования</span></h1>
                            <p>В наличии широкий ассортимент строительного инструмента и оборудования. <span> На базе нашего пункта проката работает мастерская, которая занимается ремонтом и техническим обслуживанием электроинструмента и бензоинструмента.</span></p>
                        </div>
                        <div className="banner-buttonblock">
                            <MyButton {...btnCatalog}/>
                            <MyButton {...btnConsul}/>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="banner-imgblock">
                            <img src={require('../img/main.png')} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}