import React from 'react';

export const MyButton = ({ btn, textBtn, classBtn, sizeBtn, link, additClasses='', id='', callback = () => {}}) => {
    if (btn)
        return (
            <button onClick={callback} id={id} className={`btn btn-${classBtn} ${additClasses} ` + (sizeBtn ? `btn-${sizeBtn}` : '')}>{textBtn}</button>
        )
    return (
        <a href={link} id={id} className={`btn btn-${classBtn} ${additClasses} ` + (sizeBtn ? `btn-${sizeBtn}` : '')}>{textBtn}</a>
    )
}