import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getPosts } from '../redux/async/products';
import { types } from '../redux/types';
import { Post } from './Post';
import { Pagination } from './UI/Pagination';

export const Posts = () => {
    const dispatch = useDispatch()
    const products = useSelector(state => state.products)
    const search = useSelector(state => state.search)
    const searchQuery = useSelector(state => state.searchQuery)
    const per_page = useSelector(state => state.per_page)
    const allProducts = useSelector(state => state.allProducts)

    useEffect(() => {
        // !products.length && dispatch(getPosts())
    }, [])

    useEffect(() => {
        !search && dispatch(getPosts())
    }, [search])

    useEffect(() => {
        const countOfButtons = Math.ceil(allProducts / per_page)
        let buttons = []
        if (countOfButtons)
            for (let i = 1; i <= countOfButtons; i++)
                buttons.push(i)
        dispatch({ type: types.SetPagination, payload: buttons })
    }, [allProducts])

    return (
        <>
            <div className="products-container">
                {search && <h5 className="search-result">Вы искали: <span>{searchQuery}</span></h5>}
                <div className="row">
                    {search && !products.length && <h4 className="no-search-result"><span>Ничего</span> не найдено</h4> }
                    {products && products.map((product, index) => <Post key={index} post={product} />)}
                </div>
            </div>
            {search ? <Pagination type={"search"} /> : <Pagination type={"products"} />}
        </>
    )
}